<template>
    <div>
        <!-- 面包屑导航 -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>商品列表</el-breadcrumb-item>
 
        </el-breadcrumb>
        <!-- 卡片区域 -->
        <el-card class="box-card">
            <el-row>
                <!-- 请输入内容 -->
                <el-col style="100%">
                    <el-input placeholder="请输入内容" clearable v-model="queryinfo" class="input-with-select" style="width:300px" @clear="getGoodsList">
                        <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
                    </el-input>
                    <el-button type="primary" style="margin-left:20px;" @click="addGoods">添加商品</el-button>
                </el-col>
            </el-row>
            <!-- 表格数据 -->
            <el-table :data="goodsList" style="width: 100%" border>
                <!-- 索引列 -->
                <el-table-column type="index" label="#"></el-table-column>
                <!-- 商品名称列 -->
                <el-table-column width="600px" prop="goods_name" label="商品名称"></el-table-column>
                <!-- 商品价格列 -->
                <el-table-column prop="goods_price" label="商品价格(元)"></el-table-column>
                <!-- 商品名称列 -->
                <el-table-column prop="goods_weight" label="商品重量"></el-table-column>
                <!-- 商品名称列 -->
                <el-table-column prop="add_time" label="创建时间">
                    <template slot-scope="scope">
                        {{scope.row.add_time | dateFormat}}
                    </template>
                </el-table-column>
                <!-- 商品名称列 -->
                <el-table-column prop="setting" label="操作">
                    <template slot-scope="scope">
                        <el-button size="mini" type="primary">编辑</el-button>
                        <el-button size="mini" type="danger" @click="deleteGoods(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
           
            </el-table>
            <!-- 分页 -->
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pagenum"
            :page-sizes="[5, 10, 20, 30]"
            :page-size="100"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
            </el-pagination>
        </el-card>
        
    </div>
</template>

<script>
export default {
    data(){
        return{
            // 搜索框数据
            queryinfo:"",
            // 当前页面
            pagenum:1,
            // 每页显示条数
            pagesize:5,
            // 总共的数据条数
            total:0,
            // 商品表格数据
            goodsList:[],
            // 当前操作的商品id
            goodsId:0,
           
        }
    },
    created(){
        // 向服务器发起请求获取数据
        this.getGoodsList()
    },
    methods:{
        // 向服务器发起请求获取数据
        async getGoodsList(){
            // 请求数据
            const {data:res} = await this.$http.get("goods",{params:{
                pagenum:this.pagenum,
                pagesize:this.pagesize,
                query:this.queryinfo
            }})
            console.log(res);
            
            // 判断服务器返回的状态码
            if(res.meta.status !== 200){
                return this.$message.error("获取数据失败")
            }
            // 获取数据成功，保存到待渲染列表中
            this.goodsList = res.data.goods
            this.total = res.data.total

            
        },
        // handleSizeChange 页面数据改变触发事件
        handleSizeChange(val){
            console.log(val);
            // 将页面数据大小进行保存
            this.pagesize = val
            // 重新获取数据
            this.getGoodsList()
            
            
        },
        // handleCurrentChange 页面页码改变触发事件
        handleCurrentChange(val){
            console.log(val);
            // 将页面页码进行保存
            this.pagenum = val
            // 重新获取数据
            this.getGoodsList()
            
        },
        // 点击删除商品按钮时，触发事件
        async deleteGoods(row){
            
            this.goodsId = row.goods_id
            // 对用户操作进行判断
            const confirmResult = await this.$confirm('是否删除此商品', '删除商品', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).catch(err=>err)
     
            // 根据用户操作进行请求
            if(confirmResult !== "confirm"){
                return
            }
            // 向服务器发起请求进行删除操作
            const {data:res} = await this.$http.delete("goods/"+this.goodsId)
  

            // 判断服务器返回的状态码
            if(res.meta.status !== 200){
                return this.$message.error("获取数据失败")
            }
            
            
            // 删除成功
            this.$message.success("删除数据成功")
            // 重新刷新页面
            this.getGoodsList()
               
        },
        addGoods(){
            // 点击添加商品按钮，跳转页面
            this.$router.push('/goods/add')
        }


    }
}
</script>

<style lang="less" scoped>

</style>